<template>
  <view class="page-item section">
    <!-- <h2>联系我</h2> -->
    <!-- <view class="page-title">联系我</view> -->
    <view class="page-container">
      <view class="experience">
        <h3>个人能力</h3>
        <view class="content" v-for="(item,index) in contact.experiences" :key="index">
          <p class="p-item">{{item}}</p>
        </view>
        <!-- {{contact.experience}} -->
      </view>
      <view class="evaluation">
        <h3>自我评价</h3>
        <view class="content">{{contact.evaluation}}</view>
      </view>
      <view class="socials">
        <h3>社交帐号</h3>
        <view class="box">
          <view v-for="(item, index) in contact.socials" :key="index">
            <uni-link v-if="item.url" class="socials-item" :href="item.url" :text="item.url">
              <!-- <i :class="`iconfont ${item.icon}`"></i> -->
              <i class="iconfont" :class="item.icon"></i>
            </uni-link>
            <view v-if="!item.url" class="socials-item" @click="isShowPop=!isShowPop">
              <i class="iconfont" :class="item.icon"></i>
            </view>
          </view>
        </view>
      </view>
      <view class="contact-me">
        <h3>联系方式</h3>
        <uni-link class="btn" :href="`tel:${contact.tel}`" :text="contact.tel">
          <i class="iconfont icon-shoujihao"></i>
          <text class="text">{{contact.tel}}</text>
        </uni-link>
        <uni-link class="btn" :href="`mailto:${contact.tel}`" :text="contact.tel">
          <i class="iconfont icon-youxiang"></i>
          <text class="text">{{contact.email}}</text>
        </uni-link>
      </view>
      <view v-show="isShowPop" class='popContainer' @click="isShowPop=!isShowPop">
        <img :src="qrCodeImage" alt="" >
      </view>
    </view>
  </view>
</template>
<script>

import { contact } from '@/data/index.js'
export default {
  name: 'Contact',
  data(){
    return {
      contact: contact,
      isShowQRCode: false,
      isShowPop: false,
      qrCodeImage: '/static/images/qrCode.png'
    }
  },
  methods: {
    copy(){
      const email = this.contact.email
      uni.setClipboardData({
        data: email, //要被复制的内容
        success: () => { //复制成功的回调函数
          uni.showToast({
            title: '复制成功',
            duration: 3000
          });
        }
      });
    },
    previewImage(e) {
      console.log('e', e);
      const qrCodeImage = this.qrCodeImage;
      uni.previewImage({
        // 需要预览的图片链接列表
        urls: [qrCodeImage],
        // 为当前显示图片的链接/索引值
        current: 0,
        // 图片指示器样式  
        indicator:'default',
        // 是否可循环预览
        loop:false,
        // 长按图片显示操作菜单，如不填默认为保存相册
        // longPressActions:{
        //  itemList:[this.l('发送给朋友'),this.l]
        // },
        success: res => {
          console.log('res', res);
        }, 
        fail: err => {
          onsole.log('err', err);
        }
      });
    },
    toOutLink(url){
      uni.navigateTo({
        url: '/pages/out-link/out-link?url='+ url
      })
    }
  }

}
</script>
<style lang="scss" scoped>

.section {
  background-color: rgb(23, 141, 150)!important;
  a {
    text-decoration: none;
  }
  .page-container {
    text-align: left;
    height: auto;
    h3 {
      // text-align: center;
      // color: #ffffff;
      margin: 10px 0px;
    }
    .experience {
      .content {
        padding-left: 15px;
      }
      .p-item {
        padding: 2px 0;
      }
    }
    .evaluation {
      .content {
        padding-left: 15px;
      }
    }
    .socials {
      .box {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .socials-item {
          display: block;
          height: 40px;
          width: 40px;
          background: rgba(11, 5, 48, 0.3);
          border-radius: 20px;
          text-align: center;
          margin: 0 5px;
          text-decoration: none;
          .iconfont {
            font-size: 2rem;
            line-height: 40px;
            color: $uni-text-color;
          }
        }
      }
    }
    .contact-me {
      .btn {
        margin: 0 auto;
        display: block;
        height: 30px;
        width: 85%;
        background: rgba(11, 5, 48, 0.3);
        border-radius: 15px;
        // font-size: 1.3rem;
        line-height: 30px;
        color: $uni-text-color;
        margin-bottom: 5px;
        text-align: left;
        // text-indent: 1rem;
        .iconfont {
          // font-size: 1.3rem;
          color: rgb(210, 213, 216);
          padding: 0 10px;
          display: inline-block;
        }
        .text {
          color: $uni-text-color;
        }
      }
    }
    .popContainer {
      position: fixed;
      top: 45%;
      left: 45%;
      background: rgba(0,0,0,0.3);
      img {
        width: 100%;
        height: 100%;
        object-fit: contain
      }
    }
  }
}
</style>
